import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import PropTypes from 'prop-types'

import 'font-awesome/css/font-awesome.min.css';
import StarRating from 'components/stars/StarRating'
import DisciplineList from "components/disciplines/DisciplineList"
import DescriptorList from "components/descriptors/DescriptorList"
import TagList from "components/tags/TagList"

import QuestionAuthor from "./QuestionAuthor"

const QuestionInfo = ({disciplines, teachingLevels, descriptors, tags, difficulty, author , rating, onRate=f=>f}) =>
            <Container className="question-information">
              <Row className="title-section-question">
                    <h4><i className="fa fa-info-circle"></i> Informao da Questo</h4>
              </Row>
              <Row>
                 <Col className="info-label" sm="4" xs="4">Disciplinas</Col>
                 <Col sm="8" xs="8">
                     <DisciplineList list={disciplines} />
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col className="info-label" sm="4" xs="4">Grau de difuldade</Col>
                 <Col sm="8" xs="8">
                    <span className="label-info difficulty-level">{difficulty}</span>
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col  className="info-label" sm="4" xs="4">Nvel de Ensino</Col>
                 <Col sm="8" xs="8">
                    <TagList list={teachingLevels} styleTag="label-info teaching-level"/>
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col  className="info-label" sm="4" xs="4">Tags</Col>
                 <Col sm="8" xs="8">
                    <TagList list={tags} styleTag="label-info tag-name"/>
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col  className="info-label" sm="4" xs="4">Descritores</Col>
                 <Col sm="8" xs="12">
                    <DescriptorList list={descriptors} styleTag="label-info descriptor-name"/>
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col  className="info-label" sm="4" xs="4">Autor</Col>
                 <Col sm="8" xs="8">
                   <QuestionAuthor author={author} styleTag="label-info author"/>
                 </Col>
              </Row>
              <Row className="row-info">
                 <Col  className="info-label" sm="4" xs="4">Avaliao</Col>
                 <Col sm="8" xs="8">
                     <StarRating onRate={(rating) => onRate(rating)} starsSelected={rating} />
                 </Col>
              </Row>
            </Container>

    QuestionInfo.propTypes = {
        rating: PropTypes.number,
        onRate: PropTypes.func
    }

    QuestionInfo.defaultProps = {
        rating: 0,
        onRate: f=>f
    }


export default QuestionInfo;



