  setFields(field, event){
    console.log(event.target.value)
    console.log(this.state)
    let prevState = this.state;
    if(prevState[field]==='on'){
      prevState[field] = '';
    } else {
      prevState[field] = event.target.value;
    }
    this.setState(prevState);
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      let prevState = this.state;
      prevState['activeTab'] = tab;
      this.setState({
        prevState
      });
    }
  }

  render(){
     return(
          <HomeUserPage>
            <div>
                <Nav tabs>
                <NavItem>
                  <NavLink
                    className={classnames({ active: this.state.activeTab === '1' })}
                    onClick={() => { this.toggle('1'); }}
                  >
                    Monte seu documento
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={classnames({ active: this.state.activeTab === '2' })}
                    onClick={() => { this.toggle('2'); }}
                   >
                    Escolha as questes
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={this.state.activeTab} style={{'display':'block'}}>
                <TabPane tabId="1">
                      <DocumentForm setFields={this.setFields}/>
                </TabPane>
                <TabPane tabId="2">
                      <DocumentPreview data={this.state}/>
                </TabPane>
                </TabContent>
            </div>
          </HomeUserPage>);
    }
