import React from "react";
import { ListGroup, ListGroupItem } from 'reactstrap';
import SidebarFilter from "components/sidebarfilter/SidebarFilter";

import 'assets/css/Navigation.css';
import 'font-awesome/css/font-awesome.min.css';

const filters= {
  "disciplines": [
    { "name": "Artes" },
    { "name": "Biologia" },
    { "name": "Cincias" },
    { "name": "Educao Fsica" },
    { "name": "Ensino Religioso" },
    { "name": "Estudos Sociais" },
    { "name": "Filosofia" },
    { "name": "Fsica" },
    { "name": "Geografia" },
    { "name": "Histria" },
    { "name": "Informtica" },
    { "name": "Lngua Estrangeira - Ingls" },
    { "name": "Lngua Estrangeira - Espanhol" },
    { "name": "Lngua Estrangeira - Outras" },
    { "name": "Matemtica" },
    { "name": "Polivalente" },
    { "name": "Portugus / Lngua Portuguesa" },
    { "name": "Qumica" },
    { "name": "Redao e Literatura" },
    { "name": "Sociologia" },
    { "name": "Outros" }
  ],
  "teachingLevels":[
    { "name": "Ensino Infantil"},
    { "name": "Ensino Fundamental I"},
    { "name": "Ensino Fundamental II"},
    { "name": "Ensino Mdio"},
    { "name": "Ensino Superior"},
    { "name": "Ensino para Jovens e Adultos (EJA)"}
  ],
  "difficultyLevels":[
    { "name": "Fcil"},
    { "name": "Mdio"},
    { "name": "Difcil"}
  ]
}

const SidebarFilters = ()=> {
    return (
          <ListGroup className="question-all-filters">
                <h6><i className="fa fa-filter"></i> Filtros</h6>
                <SidebarFilter name="Disciplinas" list={filters.disciplines}/>
                <SidebarFilter name="Nvel de Ensino" list={filters.teachingLevels}/>
                <SidebarFilter name="Dificuldade" list={filters.difficultyLevels}/>
          </ListGroup>


      )
}

export default SidebarFilters;



